import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { GetDataGroup, getDataSales } from "../../../../actions/datamaster_action";
import {
  InputDate, ReanderSelect,
  // ReanderSelect,
} from "../../../../components/helpers/field";
import { formatGram, getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiLaporanPenjualanSales from "../../../../Validasi/Penjualan/ValidasiLaporanPenjualanSales";
import ExportExelPembelianSales from "./exel/ExportExelPembelianSales";
import ExportLaporanPembelianSales from "./pdf/ExportLaporanPembelianSales"
import Loading from 'react-fullscreen-loading';
// const maptostate = (state) => {
//   return {
//     datasales: state.datamaster.getDataSales,
//   };
// };
class HeadFormLaporanPembelianSales extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  componentDidMount() {
    this.props.dispatch(getDataSales())
    this.props.change("kode_sales","ALL")
    this.props.dispatch(GetDataGroup());
    this.props.change("kode_group", "ALL");
    this.props.change("jenis_group", "ALL");

  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  exportPdf() {
    ExportLaporanPembelianSales(this.props.LaporanTransaksiPembelian);
  }
  render() {
    let datagroup = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.GetDataGroup.forEach((list) => {
      let row = {
        name: list.kode_group,
        value: list.kode_group,
      };
      datagroup.push(row);
    });
    let databarang = [];
    let berat = 0;
    let harga = 0;
    let jumlah = 0;
    this.props.LaporanTransaksiPembelian.forEach((element) => {
      // element.detail.forEach((detail) => {
      let row = {
        kode_sales: element._id.kode_sales,
        berat: element.berat,
        harga: element.harga,
        jumlah: element.jumlah
        }
        databarang.push(row);
        berat = berat + element.berat;
        harga = harga + element.harga;
        jumlah = jumlah + element.jumlah;
       
      // });
    });
    const columns = [
      
      // {
      //   dataField: "no_faktur_beli",
      //   text: "No FB",
      //   footerAttrs: {
      //     colSpan: "4",
      //   },
      //   footer: (cell, row, num, index) => {
      //     return <div>Total Barang : {databarang.length} </div>;
      //   },
      // },
      
      {
        dataField: "kode_sales",
        text: "Kode Sales",
        footer: "",
        // footerAttrs: {
        //   colSpan: "3",
        // },
        // footer: (cell, row, num, index) => {
        //   return <div>Total Barang : {databarang.length} </div>;
        // },
      },
     
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{formatGram(berat,3)} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right"> {formatGram(cell,3)} </div>;
        },
      },
     
      {
        dataField: "jumlah",
        text: "Total Qty",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{jumlah.toLocaleString("kr-KO")} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell.toLocaleString("kr-KO")}</div>;
        },
      },
      {
        dataField: "harga",
        text: "Total Pembelian Rp",
        headerClasses: 'text-right',
        footer: (cell, row, num, index) => {
          return <div className="text-right">{harga.toLocaleString("kr-KO")} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{cell.toLocaleString("kr-KO")}</div>;
        },
      },
     
    ];
    let sales = [
      {
        value: "ALL",
        name : "SEMUA"
      }
    ]
    this.props.datasales.map((row) => {
      let data = {
        value: row.kode_sales,
        name: row.kode_sales + " - " + row.nama_sales,
      };
      sales.push(data);
      return true
    })
    return (
      <form onSubmit={this.props.handleSubmit} onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }} autoComplete="off">
        <div className="row">
        <div className="col-lg-4">
          <Field
            name="tgl_awal"
            component={InputDate}
            label="Tanggal Dari"
            type="text"
            selected={this.state.tgl_awal}
            onChange={(date) => this.setStartDate(date)}
            placeholder="Masukan Tanggal Dari"
          />
        </div>

        <div className="col-lg-4">
          <Field
            name="tgl_akhir"
            component={InputDate}
            type="text"
            selected={this.state.tgl_akhir}
            onChange={(date) => this.setLastDate(date)}
            label="Tanggal Akhir"
            placeholder="Masukan Tanggal Akhir"
          />
        </div>
          <div className="col-lg-4">
            <Field
              label="Kode Sales"
              name="kode_sales"
              options={sales}
              placeholder={"Silahkan Pilih Nama Sales"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="kode_group"
              component={ReanderSelect}
              options={datagroup}
              onChange={(e) => this.setState({ kode_group: e })}
              label="Kode Group"
              placeholder="Pilih Kode Group"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="jenis_group"
              component={ReanderSelect}
              options={[
                {
                  value: "ALL",
                  name: "SEMUA",
                },
                {
                  value: "MUDA",
                  name: "MUDA",
                },
                {
                  value: "TUA",
                  name: "TUA",
                },
              ]}
              label="Jenis Group"
              placeholder="Pilih Jenis Group"
            />
          </div>
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="kode_barcode"
              data={this.props.export ? databarang : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Transaksi Pembelian Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
          {/* <label htmlFor="">&nbsp;</label> */}
          <div className="row">
            <div className="col-lg-6">
                <button
                  type="button"
                onClick={() => this.exportPdf()}
                className="btn btn-warning btn-block"
              >
                {" "}
                Export PDF{" "}
              </button>
            </div>
            <div className="col-lg-6">
              <ExportExelPembelianSales data={this.props.LaporanTransaksiPembelian} />
            </div>
          </div>
        </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadFormLaporanPembelianSales = reduxForm({
  form: "HeadFormLaporanPembelianSales",
  enableReinitialize: true,
  validate: ValidasiLaporanPenjualanSales,
})(HeadFormLaporanPembelianSales);
// export default connect(maptostate, null)(HeadFormLaporanPembelianSales);
export default connect((state) => {
  return {
    datasales: state.datamaster.getDataSales,
    GetDataGroup: state.datamaster.GetDataGroup,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormLaporanPembelianSales);
