import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  formatGram,
  getToday,
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const ExportLaporanPembelianSales = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = ""
) => {
  // initialize jsPDF
  const doc = new jsPDF("L", "mm");
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let finalY = 30;
  // let total_baraang = 0;

  doc.setFontSize(15);
  doc.text("LAPORAN PEMBELIAN SALES", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );

  doc.setProperties({
    title: "LAPORAN PEMBELIAN PRAMUNIAGA",
  });
  //row 1
  
  let tableColumn = [
    [
      {
        content: `SALES`,
      },
      {
        content: `BERAT`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `TOTAL QTY`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `TOTAL PEMBELIAN RP`,
        styles: {
          halign: "right",
        },
      },
      // {
      //   content: `NO FAKTUR`,
      // },
      // {
      //   content: `NAMA CUSTOMER`,
      // },
      // {
      //   content: `NO HP`,
      // },
      // {
      //   content: `ALAMAT CUSTOMER`,
      // },
      // {
      //   content: `KODE GUDANG`,
      // },
      // {
      //   content: `KODE DEPT`,
      // },
      // {
      //   content: `BERAT NOTA`,
      // },
      // {
      //   content: `BERAT`,
      // },
      // {
      //   content: `HARGA NOTA`,
      // },
      // {
      //   content: `HARGA`,
      // },
      // {
      //   content: `HARGA RATA`,
      // },
      // {
      //   content: `KONDISI`,
      // },
    ],
  ];
  let sub_jumlah = 0;
  let sub_harga = 0;
  let sub_berat = 0;
  data.forEach((item, index) => {
    // let head = [
    //   {
    //     content: `KODE SALES : ${item._id.kode_sales}`,
    //     colSpan: 12,
    //   },
    // ];
    // tableRows.push(head);

    let rows = [
      item._id.kode_sales,
      // item.no_faktur_beli,
      // item.nama_customer,
      // item.no_hp,
      // item.alamat_customer,
      // item.kode_gudang,
      // item.kode_dept,
      // item.jumlah,
      // parseInt(item.harga).toLocaleString("kr-KO"),
      {
        content: formatGram(item.berat, 3),
        styles: {
          halign: "right",
        },
      },
      {
        content: parseInt(item.jumlah),
        styles: {
          halign: "right",
        },
      },
      {
        content: parseInt(item.harga).toLocaleString("kr-KO"),
        styles: {
          halign: "right",
        },
      },
      // item.berat.toFixed(1),
      // parseInt(item.harga_nota).toLocaleString("kr-KO"),
      // parseInt(item.harga_rata).toLocaleString("kr-KO"),
      // item.kondisi,
    ];

    sub_jumlah = sub_jumlah + parseFloat(item.jumlah);
    sub_harga = sub_harga + parseFloat(item.harga);
    sub_berat = sub_berat + parseFloat(item.berat);
    tableRows.push(rows);

    // total_baraang = total_baraang+item.detail.length;
    // localStorage.setItem('hasil',JSON.stringify(item.detail.length))
    // console.log();
  });
  let footer = [
    {
      content: ``,
      // colSpan: ,
      styles: {
        halign: "center",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${sub_berat.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: "right",
      },
    },

    {
      content: sub_jumlah.toLocaleString("kr-KO"),
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: "right",
      },
    },
    {
      content: sub_harga.toLocaleString("kr-KO"),
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: "right",
      },
    },
  ];
  tableRows.push(footer);
  let printed = [
    {
      colSpan: 9,
      content: `Printed By ${
        JSON.parse(localStoragedecryp("userdata")).user_id
      } / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ];
  tableRows.push(printed);
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      fillColor: "#E8E5E5",
      textColor: "#000",
    },
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  footRows = [];
  // sub_harga = 0;
  // sub_harga_rata = 0;
  // sub_harga_nota = 0;
  // sub_berat = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  // doc.text(
  //   `User                        : ${
  //     JSON.parse(localStoragedecryp("userdata")).user_id
  //   }`,
  //   14,
  //   finalY + 10
  // );
  // doc.text(`Cetak                      : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN PEMBELIAN PRAMUNIAGA.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN PEMBELIAN PRAMUNIAGA</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default ExportLaporanPembelianSales;
