import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class ExportExelPembelianSales extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let total_berat = 0;
    let total_barang = 0;
    let harga = 0;
    let jumlah = 0;
    this.props.data.forEach((item, index) => {
      // barang.detail.forEach((item, index) => {
      total_berat = total_berat + parseFloat(item.berat);
      total_berat = total_berat + parseFloat(item.berat);
      harga = harga + item.harga;
      jumlah = jumlah + item.jumlah;
      total_barang = total_barang + item.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Pembelian Pramuniaga"
          sheet="Laporan Pembelian Pramuniaga"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="4"> Laporan Pembelian Sales </th>
            </tr>
            <tr>
              <th colSpan="4">
                {" "}
                Periode{" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="4"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="4"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                SALES
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "center",
                }}
              >
                BERAT
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                TOTAL QTY
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                }}
              >
                TOTAL PEMBELIAN RP
              </th>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((row, index) => (
              <>
                {/* {item.detail.map((row, index) => ( */}
                <tr>
                  <td>{row._id.kode_sales}</td>
                  <td
                    style={{
                      textAlign: "right",
                    }}
                  >
                    {formatGram(row.berat,3)}
                  </td>
                  <td style={{textAlign: "right"}}>{row.jumlah}</td>
                  <td style={{textAlign: "right"}}>{row.harga.toLocaleString("kr-KO")}</td>
                </tr>
                {/* ))} */}
              </>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <td
                // colSpan="6"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",

                  textAlign: "center",
                }}
              ></td>

              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                {formatGram(total_berat,3)}
              </td>

              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                {jumlah}
              </td>

              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                {harga.toLocaleString("kr-KO")}
              </td>
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default ExportExelPembelianSales;
